














































import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import ProfileModel from "../models/Profile";
import NavBar from "@/components/navbar/NavBar.vue";
import { VTextField, VRadioGroup, VRadio, VBtn, VSnackbar } from "vuetify/lib";

const profile = namespace("Profile");

@Component({
  components: {
    NavBar,
    VTextField,
    VRadioGroup,
    VRadio,
    VSnackbar,
    VBtn,
  },
})
export default class Profile extends Vue {
  private profile: ProfileModel = new ProfileModel("", 0, 0, 0, "ru");
  private snackbar = false;

  public async mounted() {
    this.profile = await this.get();
  }

  public saveProfileHandler(): void {
    this.saveProfile(this.profile);
    this.$i18n.locale = this.profile.getLocale();
    this.snackbar = true;
  }

  @profile.Action
  saveProfile!: (profile: ProfileModel) => void;

  @profile.Action
  get!: () => ProfileModel;
}
